<template>
  <div class="text-center" id="aaa">
    <nav>
      <ul class="pagination" v-if="count">
        <!---->
        <li :class="{'disabled':current == 1}" @click='current !=1 && goTo(--current)'>
          <span class='po'>&laquo;</span>
        </li>

        <!---->
        <li v-for="index in pages" @click="current != index && goTo(index)" :class="{'active c-not':current == index}">
          <span class="po">{{index}}</span>
        </li>

        <!---->
        <li :class="{'disabled':allPage==current || allPage ==1}"
            @click="allPage!=current && allPage !=1 && goTo(++current)">
          <span class="po">&raquo;</span>
        </li>

        <!---->
        <li>
          <span class="toPage"><input v-model="inputValue" @keyup.enter="goTo()">/{{allPage}}</span>
        </li>
      </ul>
      <span v-else=""></span>
    </nav>
  </div>
</template>

<script>
  export default {
    props: ['count', 'reset'],
    data: function () {
      return {
        showItem: 10,
        allPage: 1,
        current: 1,
        inputValue: 1,
      }
    },
    methods: {
      goTo: function (byCurrent) {
        if (byCurrent) {  //
          this.current = byCurrent; //
          this.inputValue = byCurrent;//
          this.$emit('getCurrent', this.current)
        } else {
          // 
          if (!/^\+?[1-9][0-9]*$/.test(this.inputValue) || this.inputValue > this.allPage) {
            alert('');
          } else {
            this.current = this.inputValue;
            this.$emit('getCurrent', this.current)
          }
        }
      }
    },
    computed: {
      pages: function () { //
        this.allPage = Math.ceil(this.count / 20); //
        if (this.reset) { //1
          this.current = 1;
          this.inputValue = 1;
        }
        let pagArr = [], i = null;
        if (this.current < this.showItem) { //10
          i = Math.min(this.showItem, this.allPage); //
          while (i) { //vue
            pagArr.unshift(i--); //
          }
        } else { //10
          let start = this.current - this.showItem / 2;//
          i = this.showItem;
          if (start > (this.allPage - this.showItem)) { //startstart
            start = (this.allPage - this.showItem) + 1
          }
          while (i--) {
            pagArr.push(start++);
          }
        }
        return pagArr
      }
    },
  }
</script>

<style scoped>
  .pagination li span.toPage {
    padding: 4px;
  }

  .pagination li span.toPage input {
    width: 35px;
    text-align: center;
    font-size: 14px;
    line-height: 1.42857143;
    color: #555;
    border: 1px solid #ccc;
    border-radius: 4px;
  }

  .po {
    cursor: pointer;
  }

  .text-center .pagination > .active.c-not span,
  .text-center .pagination > .active.c-not span:focus,
  .text-center .pagination > .active.c-not span:focus:hover {
    cursor: not-allowed;
  }

  .pagination > .active > a, .pagination > .active > a:focus,
  .pagination > .active > a:hover, .pagination > .active > span,
  .pagination > .active > span:focus, .pagination > .active > span:hover{
    background-color: #2c7fd2;
  }

</style>



